/* Slider */
.slider__wrapper {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}

.slider__line {
  display: flex;
  transition: all 1s;
}

.slider__line img {
  width: 100%;
}

.slider__arrow {
  position: absolute;
  top: 50%;
  width: auto;
  height: auto;
  padding: 16px 10px;
  transition: 0.6s;
  border: 1px solid #fff;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2.5px);
  border-radius: 50%;
}

.slider__arrow-prev {
  left: 1%;
}

.slider__arrow-next {
  right: 1%;
}

.slider__arrow-prev:active,
.slider__arrow-next:active {
  transform: scale(0.8);
}

.slider__pagination {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.slider__dot {
  width: 16px;
  height: 16px;
  border: 1px solid #fff;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2.5px);
  border-radius: 50%;
}

.slider__dot-active {
  background: #000000;
}

.slider__pagination {
  margin-top: 20px;
}

